import commonButton from '../common/common-button.vue';
export default {
	components:{
		commonButton
	},
	props: {
		orderStatus:String,
		item: Object,
		index:Number
	},
	methods: {
		 
		open() {
			uni.navigateTo({
				url: '/pages/detail/detail?id='+this.item.productId
			});
		},
		toComment(){
			uni.navigateTo({
				url: '/pages/order/comment?orderItemId='+this.item.id
			})
		}
	},
}
